@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@300..800&family=Playfair+Display:wght@400..900&display=swap');
.title{
    font-family: "Playfair Display", serif;
    font-optical-sizing: auto;
    font-weight: 500;
    font-style: normal;
}

.title-services{
    font-family: "Playfair Display", serif;
    font-optical-sizing: auto;
    font-weight: 400;
    font-style: normal;
}

.mini-tital{
    font-family: "Open Sans", sans-serif;
    font-optical-sizing: auto;
    font-weight: 400;
    font-style: normal;
    font-variation-settings:
      "wdth" 100;
}

.faq{
    font-family: "Playfair Display", serif;
    font-style: normal;
    }

    @keyframes bounce-left {
        0%, 100% {
          transform: translateX(4px);
        }
        50% {
          transform: translateX(8px);
        }
      }
      
      @keyframes bounce-right {
        0%, 100% {
          transform: translateX(-4px);
        }
        50% {
          transform: translateX(-8px);
        }
      }
      
      .animate-bounce-left {
        animation: bounce-left 1.5s infinite;
      }
      
      .animate-bounce-right {
        animation: bounce-right 1.5s infinite;
      }
      